.planet span > img {
    height: 25px;
    width: 25px;
}
.planet span > svg {
    height: 35px;
    width: 35px;
    color: black;
}
.planet{
    border-radius: 5px;
    background: var(--green);
    margin: 20px;
    display: flex;
    flex-direction: column;
    height: 100px;
    width: 100px;
    text-shadow: 2px 2px 2px rgb(0, 0, 0, 0.3);
}

.result{
    display: flex;
    justify-content: space-around;
    height: 100%;
    flex-wrap: wrap;
}

.sign-logo{
    /* border: 1px solid rgb(0, 0, 0, 0.2); */
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-self: center;
    align-items: center;
    /* margin: 1px; */
    border-radius: 5px;
    /* background: var(--greenLight); */
}
.sign-logo > span {
    font-size: 30px;
    margin-top: 10px;
    text-shadow: none;

}

@media (max-width: 500px) { 

    .planet{
        height: 80px;
        width: 85px;
        margin: 5px
    }

    .planet > h3 {
        font-size: 16px;
    }
    .planet > strong {
        font-size: 14px;
    }

    
 }

 
